* {
    margin: 0;
    padding: 0;
    }
    body {
    /* display: flex; */
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    }
    .container {
    position: relative;
    width: 1160px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    transform-style: preserve-3d;
    perspective: 500px;
    margin: auto;
    }
    .container .box {
    position: relative;
    width: 275px;
    height: 275px;
    background: #000;
    transition: 0.5s;
    transform-style: preserve-3d;
    overflow: hidden;
    margin-right: 15px;
    margin-top: 45px;
}
.container .box .imgBx {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
}
.container .box .imgBx:before {
content: '';
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
background: linear-gradient(180deg,rgb(29, 86, 165),#000);
z-index: 1;
opacity: 0;
transition: 0.5s;
mix-blend-mode: multiply;
}
.container .box .content h2 {
color: #fff;
transition: 0.5s;
text-transform: uppercase;
margin-bottom: 5px;
font-size: 20px;
transform: translateY(200px);
transition-delay: 0.3s;
}
    
.container .box .content {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
z-index: 1;
display: flex;
padding: 20px;
align-items: flex-end;
box-sizing: border-box;
}
.container .box .content p {
color: #fff;
transition: 0.5s;
font-size: 14px;
transform: translateY(200px);
transition-delay: 0.4s;
}
.container .box .imgBx img {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
object-fit: cover;
}
.container{
    padding-bottom: 50px;
}
@media only screen and (min-width: 770px){
        .container:hover .box {
            transform: rotateY(25deg);
            }
            .container .box:hover ~ .box {
            transform: rotateY(-25deg);
            }
            .container .box:hover {
            transform: rotateY(0deg) scale(1.25);
            z-index: 1;
            box-shadow: 0 25px 40px rgba(0,0,0,0.5);
            }
            .container .box:hover .imgBx:before {
            opacity: 1;
            }
            .container .box:hover .content h2 {
            transform: translateY(0px);
            }
            .container .box:hover .content p {
            transform: translateY(0px);
            }
}

    @media only screen and (max-width: 579px) {
        .container{
            width: auto;
        }
        .container .box:hover .content h2 {
            transform: translateY(0px);
            }
            .container .box:hover .content p {
            transform: translateY(0px);
            }
            .container .box:hover .imgBx:before {
                opacity: 1;
                }
    }