nav {
    position: fixed;
    top: 0;
    width: 100%;
    transition: 1s;
    z-index: 2;
    background-color: rgba(26, 25, 25, 0.699)
}

.activeNavBar {
    background: rgba(0, 0, 0, 0.952);
    transition: 1s;
}

nav ul {
    list-style: none;
    overflow: hidden;
    color: #fff;
    padding: 0;
    text-align: right;
    margin: 0;
    background: rgba(0, 0, 0, 0);
    transition: 1s;
}

nav.black ul {
    background: #000;
}

nav ul li {
    display: inline-block;
    padding: 0.5rem 1rem;
}

nav ul li a:hover {
    color: red;
    transition: 1s;
}

nav ul li a:active{
    color: red;
}
nav ul li a {
    text-decoration: none;
    color: #fff;
    font-family: 'Times New Roman', Times, serif;
    font-size: 170%;
}

